import React, { Component } from 'react';
import {
  Widget,
  addResponseMessage,
  addLinkSnippet,
  addUserMessage
} from 'react-chat-widget';
import 'react-chat-widget/lib/styles.css';

export default class Chat extends Component {
  componentDidMount() {
    addResponseMessage(
      'Ol eu sou a Kitney Spears! Seja bem-vindo ao SJ90, qual sua dvida?'
    );
  }

  handleNewUserMessage = () => {
    console.log('New message incomig!');
    // Now send the message throught the backend API
  };

  render() {
    return (
      <Widget
        handleNewUserMessage={this.handleNewUserMessage}
        profileAvatar="https://media.giphy.com/media/KKjjVZ1VuoKas/giphy.gif"
        title="Canal de Comunicao"
        subtitle="Atendente: Kitney Spears"
      />
    );
  }
}
